import { Component } from '@angular/core';
import { 
	NavController,
 	NavParams,
 	LoadingController,
 	Loading,
  AlertController
 } from 'ionic-angular';

import {
	JavaApiProvider
} from '../../providers/java-api/java-api';

import {
	IotApiProvider
} from '../../providers/iot-api/iot-api';

import {
  InvoicePage
} from '../invoice/invoice';

import {
  Toast
} from '../ToastReplacement';


import { ActionSheetController } from 'ionic-angular';

/**
 * Generated class for the StopstoPickPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-stopsto-pick',
  templateUrl: 'stopsto-pick.html',
})
export class StopstoPickPage {
  
  route : any = {list: [] };
  refreshId : any;
  loader : Loading;
  forceLocal : boolean;
  truck : any;
  location : any;
  customer: any;
  stops : any = [];
  routeName : string;
  aisles : any = [];

  constructor(
  	public navCtrl: NavController,
  	public navParams: NavParams,
  	public japi : JavaApiProvider,
  	public iapi : IotApiProvider,
  	public loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    public actionSheetController: ActionSheetController
   ) {


    this.truck = navParams.get("truck");
  	this.route._id = navParams.get("id");
    this.forceLocal = navParams.get("fc") ? true : false;
    this.stops = navParams.get("route");
    this.routeName = navParams.get("routeName");

    if(this.forceLocal){
      this.japi.http.post(`${this.japi.URL}record_log`, {
        name : this.truck,
        text : "Driver open product scan screen"
      }, this.japi.httpOptions)
      .subscribe(
          (res : any) => {

          }
        )
    }



  	this.refreshData();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad StopstoPickPage');
  }

  count(list){
    var result = 0;
    for (var i = list.length - 1; i >= 0; i--) {
      if(list[i].amt)
       result += list[i].amt;
    }
    return result;
  }

  getLocation(){

     if(!this.route.location)
       return;

       
      this.japi.http.get(`${this.japi.URL}locations/${this.route.location}`, this.japi.httpOptions)
      .subscribe(
          (res : any) => {
            if(!res.data){
              this.getCustomerLocation();
              return;
            }
             this.location = res.data;
            
          }
      )
  }

  getCustomerLocation(){

     this.japi.http.get(`${this.japi.URL}customers/${this.route.location}`)
      .subscribe(
          (res : any) => {
            if(!res.data){
              return;
            }
            this.location = res.data;
            
          }
      )
  }

  open(invoice, customer){
      this.navCtrl.push(
      InvoicePage,
      {
        invoice : invoice,
        customer : customer
      })
  }

  getStatus(item){
    let key = "scanned";
    if(this.forceLocal){
      key = "delivered";
    }

    return ( item.amt - ( item[key] ? item[key] : 0  ) == 0 ) ? 'success' : 'danger';

  }

  localScan(item){
      if(item.delivered == item.amt){
          Toast.show('This item has been completely scanned.','','');
          return;
      }

     let alert = this.alertCtrl.create({
      title: 'Enter barcode',
      inputs: [
        {
          name: 'bar',
          placeholder: 'PRODUCT BARCODE'
        }
      ],
      buttons: [
        'Cancel',
        {
          text: 'Confirm',
          handler: data => {
            if(!data.bar)
              return;

              if(!item.barcode || item.barcode.indexOf(data.bar) == -1){
                Toast.show("Error, wrong item scanned", '','');
                return;
              }

              if(!item.delivered)
                item.delivered = 0;

              item.delivered++;

              this.backUpInvoice(this.route);
              this.checkIfDelivered();
            
          }
        }
      ]
    });
    alert.present();
  }

  checkIfDelivered(){
    let amountLeft = 0;

    for (var i = this.route.list.length - 1; i >= 0; i--) {
      let item = this.route.list[i];
      amountLeft += item.amt - ( item.delivered ? item.delivered : 0  );

    }

    if(amountLeft == 0){
        this.navCtrl.push(
          InvoicePage,
          {
            customer : this.customer,
            invoice : this.route
          }
        )
    }

  }

  scan(item){

      if(this.forceLocal){
        this.localScan(item);
        return;
      }

      if(item.scanned == item.amt){
          Toast.show('This item has been completely scanned.','','');
          return;
      }
    

     let alert = this.alertCtrl.create({
      title: 'Enter barcode',
      inputs: [
        {
          name: 'bar',
          placeholder: 'PRODUCT BARCODE'
        }
      ],
      buttons: [
       {
          text: 'Use camera',
          handler: () => {      
              this.iapi.scanBarcode()
              .then((code) => {
                  if(!code || code == "" || !item.barcode || item.barcode.indexOf(code) == -1){
                    Toast.show("Error, wrong item scanned", '','');
                    return;
                  }
                item.barcode = code;
                this.updateItem(item);
              })
              .catch((err) => {
                Toast.show("Error, barcode was not scanned!","","");
              });

          
          }
        },
        'Cancel',
        {
          text: 'Confirm',
          handler: data => {

            if(!data.bar){
              Toast.show("Error, no barcode entered", '','');
              return;
            }

              if(!item.barcode || item.barcode.indexOf(data.bar) == -1){
                Toast.show("Error, wrong item scanned", '','');
                return;
              }

              item.barcode = data.bar;

              this.updateItem(item);
          }
        }
      ]
    });
    alert.present();
  }

  async print(invoice){
   


     const actionSheet = await this.actionSheetController.create({
      title: 'Print actions',
      buttons: [{
        text: 'Print all picking tickets',
        handler: () => {
          
          var labels = [];
          let indexOf = this.stops.indexOf(this.location._id);
          
          for (var i = invoice.list.length - 1; i >= 0; i--) {
            var item = invoice.list[i];
            for(var v = 0; v < item.amt;v++){
               let label = {
                customer : this.customer.name,
                address : this.location.address,
                id : this.route.id,
                po : this.route.po,
                stop : indexOf === -1 ? "STOP NOT FOUND!" : indexOf + 1,
                route : this.routeName,
                current : v + 1,
                total : item.amt
              }
              labels.push(label);
            }

          }

           generateAllShipLabel(labels)
           .then(shipLabel => {
                this.iapi.printSticker(shipLabel);
           });
        }
      }, {
        text: 'Print invoice',
        handler: () => {
           this.iapi.print(invoice);
        }
      },
      {
        text: 'Cancel',
        role: 'destructive',
        handler: () => {
           
        }
      }]
    });
    await actionSheet.present();

  }
  updateItem(item){
  	this.loader = this.loadingCtrl.create({
		  content: `Updating ${item.itemName}`
	  });
    this.loader.present();

    let indexOf = this.stops.indexOf(this.location._id);

    if(!item.scanned)
        item.scanned = 0;

    let label = {
      customer : this.customer.name,
      address : this.location.address,
      id : this.route.id,
      po : this.route.po,
      stop : indexOf === -1 ? "STOP NOT FOUND!" : indexOf + 1,
      route : this.routeName,
      current : item.scanned + 1,
      total : item.amt
    }
     generateShipLabel(label)
    .then(shipLabel => {

        this.iapi.printSticker(shipLabel);
        this.japi.http.post(`${this.japi.URL}scan_item/${this.route._id}`, item, this.japi.httpOptions  )
        .subscribe(
          (res : any) => {

            if(res.data.error)
            Toast.show(res.data.error, "","");

            this.loader.dismiss();
            this.refreshData();
          }
        )

    });

 

  }

  backUpInvoice(route){
     this.japi.http.put(`${this.japi.URL}invoices/${this.route._id}`, route, this.japi.httpOptions  )
    .subscribe(
      (res : any) => {
        console.log("Invoice backed up.");
      }
    )
  }

  refreshData(){
  	this.japi.presentLoading();
  	this.japi.http.get(`${this.japi.URL}invoices/${this.route._id}`, this.japi.httpOptions)
  	.subscribe(
  		(res : any) => {
  			this.japi.hideLoading();
  			this.route = res.data;
        this.route.list.sort(this.sortByProximity);


        this.generateAisles(this.route);
        this.getCustomer();
        this.getLocation();
  		}
  	)
  }

  parseInt(int){
    return parseInt(int);
  }

  generateAisles(invoice){

    for (var i = invoice.list.length - 1; i >= 0; i--) {
      var item = invoice.list[i];

      if(this.aisles.indexOf(parseInt(item.aisle) ) === -1){
           this.aisles.push(parseInt(item.aisle));
      }
    }

    this.aisles.sort(this.sortNumber);
    console.log(this.aisles);
  }

  sortNumber(a, b) {
    return a - b;
  }

  sortByProximity(a , b){
    if(!a.aisle || !b.aisle)
        return 0;


    if(parseInt(a.aisle) > parseInt(b.aisle) ){
      if(parseInt(a.shelf) > parseInt(b.shelf) ){
          if(parseInt(a.slot) > parseInt(b.slot) ){
            return -1;
          } else {
            return 1;
          }
      }
      return -1;
    }

    return 1;
  }

  getCustomer(){
    this.japi.http.get(`${this.japi.URL}customers/query?name=${this.route.customer}`, this.japi.httpOptions)
    .subscribe(
      (res : any) => {
      
          this.customer = res.data;
          this.route.customerEmail = this.customer.email;
          this.route.customerPhone = this.customer.contactNo;
          this.route.customerAddress = this.customer.address; 
      }
    )
  }

}

function generateAllShipLabel(labels){
  return new Promise((resolve, reject) => {

      window["root-rec"].innerHTML = ` <style type="text/css">
        table {
          border-spacing: 5px;
        }

        #act-rec h1 {
          font-size : 2em;
          font-weight:bold;
        }
      </style>
     `;
      for (var i = labels.length - 1; i >= 0; i--) {
        var label = labels[i];
         
         let labelTemplate = `
     <div id="act-rec" style="width:320px;font-size:15px;margin-bottom:8px;">
      <img style="position:absolute;left:10px;width:65px;" src="data:image/png;base64,">
      <table style="margin-left:105px;">
        <tr>
          <th align="left" width="220">Java Traders of Vermont</th>
          <th align="left"  style="padding-left: 10px;" width="220">${label.customer}</th> 
        </tr>
        <tr>
          <td>400 Patchen Rd<br>
        South Burlington VT 05403</td>
           <td  style="padding-left: 10px;">${label.address}</td>
        </tr>
      </table>
      <h3 style="margin-top: 10px;text-align: center;"><b>INVOICE: ${label.id}</b><br/>
      PO : ${label.po}</h3>
      <table >
        <tr>
          <th  width="183"><u><b>STOP</b></u></th>
          <th  width="183"><u><b>ROUTE</b></u></th>
          <th  width="183"><u><b>PIECE</b></u></th>
        </tr>
        <tr>
            <td><h1 style="text-align: center; margin-top: 0px;color:#333;">${label.stop}</h1></td>
            <td><h1 style="text-align: center; margin-top: 0px;color:#333;">${label.route}</h1></td>
            <td><h1 style="text-align: center; margin-top: 0px;color:#333;">${label.current}-${label.total}</h1></td>
        </tr>
      </table>
      </div>`;
         window["root-rec"].innerHTML += labelTemplate;
      }
     
      setTimeout(() => {
             window["html2canvas"](document.getElementById("act-rec")).then(canvas => {
                resolve(`<img style="width:130px;" src="${canvas.toDataURL()}" />`)
             , err => console.log(err) });
      }, 1100 )
        
  })
}

function generateShipLabel(label){
  return new Promise((resolve, reject) => {

      let labelTemplate = `
      <style type="text/css">
        table {
          border-spacing: 5px;
        }

        #act-rec h1 {
          font-size : 2em;
          font-weight:bold;
        }
      </style>
      <div id="act-rec" style="width:320px;font-size:15px;">
      <img style="position:absolute;left:10px;width:65px;" src="data:image/png;base64,">
      <table style="margin-left:105px;">
        <tr>
          <th align="left" width="220">Java Traders of Vermont</th>
          <th align="left"  style="padding-left: 10px;" width="220">${label.customer}</th> 
        </tr>
        <tr>
          <td>400 Patchen Rd<br>
        South Burlington VT 05403</td>
           <td  style="padding-left: 10px;">${label.address}</td>
        </tr>
      </table>
      <h3 style="margin-top: 10px;text-align: center;"><b>INVOICE: ${label.id}</b><br/>
      PO : ${label.po}</h3>
      <table >
        <tr>
          <th  width="183"><u><b>STOP</b></u></th>
          <th  width="183"><u><b>ROUTE</b></u></th>
          <th  width="183"><u><b>PIECE</b></u></th>
        </tr>
        <tr>
            <td><h1 style="text-align: center; margin-top: 0px;color:#333;">${label.stop}</h1></td>
            <td><h1 style="text-align: center; margin-top: 0px;color:#333;">${label.route}</h1></td>
            <td><h1 style="text-align: center; margin-top: 0px;color:#333;">${label.current}-${label.total}</h1></td>
        </tr>
      </table>
      </div>`;
         window["root-rec"].innerHTML = labelTemplate;


         setTimeout(() => {
             window["html2canvas"](document.getElementById("act-rec")).then(canvas => {
                resolve(`<img style="width:130px;" src="${canvas.toDataURL()}" />`)
             , err => console.log(err) });
           }, 1100 )
        
  })
 
}





